@import "../style.scss";

.drop-content-visible {
  display: flex;
  flex-direction: column;

  .page {
    padding: 1rem;
    margin-left: 1rem;
    a {
      font-size: 2rem;
    }
    h2 {
      font-size: 2rem;
    }
  }
  #icon {
    color: $l_grey;
    font-size: 4rem;
    margin-bottom: -1rem;
    margin-top: -2rem;
    :hover {
      color: $d_grey;
    }
  }
}
.drop-content-hidden {
  display: none;
}
